body {
  padding: 20px;
}

#root {
    width: 980px;
    margin: 0 auto;
}

.container::after {
  display: block;
  content: "";
  clear: both;
}

.unmatched {
  margin-top: 30px;
}

.matched .block:nth-child(2) {
  position: relative;
  margin-left: 17px;
}
.matched .block:nth-child(2)::before {
  height: 3px;
  background: black;
  width: 20px;
  content: '';
  display: block;
  position: absolute;
  right: 100%;
  top: 50%;
  margin-top: -3px;
}

.matched .correct .block:nth-child(2)::before {
  background: green;
}
.matched .correct .block{
  border-color: green;
}

.matched .incorrect .block:nth-child(2)::before {
  background: red;
}
.matched .incorrect .block{
  border-color: red;
}

.col {
  float: left;
  width: 50%;
}

.block {
  display: inline-block;
  vertical-align: top;
  padding: 5px 10px;
  border: 3px solid black;
  border-radius: 5px;
}

.unmatched .block {
  cursor: pointer;
}

.unmatched .block:hover {
  border-color: purple;
}

.block-row{
  margin-top: 10px;
}
.block-row:first-child{
  margin-top: 0;
}

.unmatched .block.selected {
  border-color: orangered;
}

.btn {
    margin-top: 10px;
    cursor: pointer;
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    color: #fff;
}

.btn-check {
    background-color: #007bff;
    border-color: #007bff;
}

.btn-play {
    background-color: green;
    border-color: green;
}

.btn-check[disabled] {
    cursor: default;
    background-color: grey;
    border-color: grey;
}